@import '~bootstrap/dist/css/bootstrap.css';
@import '~angular-material/angular-material.css';
@import '~angularjs-datepicker/dist/angular-datepicker.css';
@import '~angular-loading-bar/build/loading-bar.css';

/**
 * Bootstrap Fonts
 */

@font-face {
    font-family: 'Glyphicons Halflings';
    src: url('/assets/fonts/bootstrap/glyphicons-halflings-regular.eot');
    src: url('/assets/fonts/bootstrap/glyphicons-halflings-regular.eot?#iefix') format('embedded-opentype'),
    url('/assets/fonts/bootstrap/glyphicons-halflings-regular.woff') format('woff'),
    url('/assets/fonts/bootstrap/glyphicons-halflings-regular.ttf') format('truetype'),
    url('/assets/fonts/bootstrap/glyphicons-halflings-regular.svg#glyphicons_halflingsregular') format('svg');
}

@import '~font-awesome/css/font-awesome.css';

/**
 *Font Awesome Fonts
 */

@font-face {
    font-family: 'FontAwesome';
    src: url('/assets/fonts/font-awesome/fontawesome-webfont.eot?v=4.1.0');
    src: url('/assets/fonts/font-awesome/fontawesome-webfont.eot?#iefix&v=4.1.0') format('embedded-opentype'),
    url('/assets/fonts/font-awesome/fontawesome-webfont.woff?v=4.1.0') format('woff'),
    url('/assets/fonts/font-awesome/fontawesome-webfont.ttf?v=4.1.0') format('truetype'),
    url('/assets/fonts/font-awesome/fontawesome-webfont.svg?v=4.1.0#fontawesomeregular') format('svg');
    font-weight: normal;
    font-style: normal;
}

/**
 * App-wide Styles
 */

.browserupgrade {
    margin: 0.2em 0;
    background: #ccc;
    color: #000;
    padding: 0.2em 0;
}

.background_color {
    background-color: white !important;
}

body {
    background-color: white !important;
}

.margin-top--120 {
    margin-top: -125px !important;
}

.md-content.md-default-theme, .md-content {
    background-color: rgb(250, 250, 250) !important;
}

.layout-padding, .layout-padding-gt-sm, .layout-padding-gt-sm > *, .layout-padding-md, .layout-padding-md > *, .layout-padding > *, .layout-padding > .flex, .layout-padding > .flex-gt-sm, .layout-padding > .flex-md {
    padding: 15px !important;
}

md-input-container label {
    color: rgb(66, 66, 66)
}

.md-whiteframe-1dp, .md-whiteframe-z1 {
    box-shadow: none;
    background: #fafafa;
}

md-select.md-default-theme .md-select-value.md-select-placeholder, md-select .md-select-value.md-select-placeholder {
    color: rgb(66, 66, 66);
    font-weight: bold !important;
}

md-input-container.md-default-theme:not(.md-input-invalid).md-input-has-value label, md-input-container:not(.md-input-invalid).md-input-has-value label {
    color: rgb(66, 66, 66);
    font-weight: bold !important;
}

